<template>
  <div class="menu">
    <slot
      name="activator"
      :open="openMenu"
      :toggle="toggleMenu"
    />
    <popup-content
      :transition="transition"
      :open="open"
      :bounds="bounds"
      :backdrop="backdrop"
      @click:in="closeMenu"
      @click:out="popupClick"
      @keydown.esc="closeMenu"
      @keydown.tab.prevent="closeMenu"
    >
      <!-- <slot
        name="menu"
        :is-open="open"
        :close="closeMenu"
      /> -->
      <slot
        name="menu"
        :is-open="open"
        :close="closeMenu"
      >
        <v-list class="popup-list" :focused="open">
          <slot name="menu-items"/>
        </v-list>
      </slot>
    </popup-content>
  </div>
</template>

<script>
import PopupContent from '@/components/PopupContent.vue'
import VList from '@/components/List.vue'

export default {
  components: { PopupContent, VList },
  props: {
    transition: {
      type: [String, Object],
      default: 'fade'
    },
    backdrop: Boolean
  },
  data () {
    return {
      open: false,
      bounds: null
    }
  },
  methods: {
    openMenu () {
      const bounds = this.$el.firstElementChild.getBoundingClientRect()
      this.bounds = bounds.toJSON()
      this.open = true
    },
    closeMenu () {
      this.open = false
    },
    toggleMenu () {
      this.open ? this.closeMenu() : this.openMenu()
    },
    popupClick (e) {
      if (!this.$el.contains(e.target)) {
        this.closeMenu()
      }
    }
  }
}
</script>
