<template>
  <div class="list">
    <document-listener
      v-if="focused"
      @keydown.up.prevent="highlightPrev"
      @keydown.down.prevent="highlightNext"
      @keydown.enter="confirm"
    />
    <slot/>
  </div>
</template>

<script>
import DocumentListener from '@/components/DocumentListener.js'

export default {
  components: { DocumentListener },
  props: {
    focused: Boolean
  },
  data () {
    return {
      highlightIndex: -1
    }
  },
  watch: {
    highlightIndex (index, old) {
      // for (const el of this.$el.querySelectorAll('.highlight')) {
      //   el.classList.remove('highlight')
      // }
      if (this.$el.children[old]) {
        this.$el.children[old].classList.remove('highlight')
      }
      const el = this.$el.children[index]
      if (el) {
        el.classList.add('highlight')
      }
    },
    focused (focused) {
      if (!focused) {
        this.highlightIndex = -1
      }
    }
  },
  methods: {
    highlightPrev () {
      this.highlightIndex = Math.max(0, this.highlightIndex - 1)
    },
    highlightNext () {
      const max = this.$el.childElementCount - 1
      this.highlightIndex = Math.min(max, this.highlightIndex + 1)
    },
    confirm () {
      const el = this.$el.children[this.highlightIndex]
      if (el) {
        const target = el.querySelector('button, a[href], [role="menuitem"]') || el
        setTimeout(() => target.click())
      }
    }
  }
}
</script>
