<template>
  <div
    class="input-field"
    :class="{focus, error: !!error}"
  >
    <label :for="inputId" v-text="label" @click="focusField"/>
    <!-- <slot :id="inputId"/> -->
    <render-func :render="renderSlot">
      <slot/>
    </render-func>
    <div class="error">
      {{ error }}
    </div>
  </div>
</template>

<script>
const RenderFunc = {
  functional: true,
  props: {
    render: Function
  },
  render (h, ctx) {
    return ctx.props.render(h)
  }
}

let counter = 1
export default {
  components: { RenderFunc },
  props: {
    label: String,
    error: String,
    id: String
  },
  data () {
    return {
      focus: false,
      _id: null
    }
  },
  computed: {
    inputId () {
      return this.id || this._id
    }
  },
  created () {
    this._id = `v-input-${counter++}`
  },
  // mounted () {
  //   const onFocus = (val) => {
  //     this.focus = val
  //   }
  //   this.$children[0].$on('focus', onFocus)
  // },
  methods: {
    focusField () {
      const inputEl = this.$el.querySelector('[tabindex]')
      if (inputEl && !inputEl.getAttribute('disabled')) {
        inputEl.focus()
      }
    },
    _onFocus (val) {
      this.focus = val
    },
    renderSlot () {
      // const comp = this.$slots.default[0]
      const comp = this.$scopedSlots.default()[0]
      // comp.componentOptions.propsData.disabled = true
      comp.componentOptions.listeners.focus = this._onFocus
      comp.data.attrs.id = this.inputId
      return comp
    }
  }
}
</script>

<style lang="scss" scoped>
.input-field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 4px;

  label {
    font-size: 12px;
    opacity: 0.6;
    margin-bottom: 1px;
  }
  > :nth-child(2) {
    margin: 0;
  }

  &.focus {
    label {
      color: teal;
      opacity: 1;
    }
  }
  &.error {
    color: red;
    label {
      color: inherit;
      opacity: 0.8;
    }
  }
  .error {
    font-size: 12px;
    opacity: 0.8;
    margin-top: 1px;
  }
}
</style>