const DocumentListener = {
  mounted () {
    setTimeout(() => {
      const listeners = {}
      for (const type in this.$listeners) {
        listeners[type] = e => this.$emit(type, e)
        document.addEventListener(type, listeners[type], true)
      }
      this.$once('hook:beforeDestroy', () => {
        for (const type in listeners) {
          document.removeEventListener(type, listeners[type], true)
        }
      })
    }, 300)
  },
  render: () => null
}

// HOC factory
// export function documentListener (opts) {

// }

export default DocumentListener
