<template>
  <div
    tabindex="0"
    role="combobox"
    class="select"
    :class="{focus, open}"
    :disabled="disabled"
    :aria-expanded="open ? 'true' : 'false'"
    @focus="focus = true"
    @blur="onBlur"
    @click="openPopup"
  >
    <slot
      name="selection"
      v-if="value && selected.item"
      v-bind="selected"
    >
      <span>{{ selected.item[itemText] }}</span>
    </slot>
    <span v-else class="placeholder">{{ placeholder }}</span>
    <svg
      width="10"
      height="10"
      viewBox="0 0 10 10"
      @click.stop="toggle"
    >
      <path d="M 1,3 L 9,3 L 5,7 Z"/>
    </svg>
    <document-listener
      v-if="focus && !open"
      @keydown.space.prevent="openPopup"
      @keydown.up.prevent="select(selected.index - 1)"
      @keydown.down.prevent="select(selected.index + 1)"
      @keydown="keyHandler"
    />
    <popup-content
      transition="slide-y"
      :open="open"
      :bounds="bounds"
      @click:out="popupClick"
      @click:in="closePopup"
      @keydown.esc="closePopup"
      @keydown.tab.prevent="closePopup"
      @keydown.up.prevent="highlight(highlightIndex - 1)"
      @keydown.down.prevent="highlight(highlightIndex + 1)"
      @keydown.enter="onEnter"
      @keydown.space.prevent=""
      @keydown="keyHandler"
      @opened="highlight(selected.index)"
    >
      <div
        class="popup-list"
        role="listbox"
        tabindex="-1"
      >
        <template v-for="(item, index) in items">
          <div
            :key="index"
            ref="item"
            tabindex="0"
            class="item"
            :class="{
              highlight: highlightIndex === index,
              selected: selected.item === item
            }"
            @click="selectItem(item)"
          >
            <slot
              name="item"
              :item="item"
              :index="index"
              :selected="selected.item === item"
            >
              <span v-text="item[itemText]"/>
            </slot>
          </div>
        </template>
      </div>
    </popup-content>
  </div>
</template>

<script>
import debounce from 'lodash/debounce'
import PopupContent from '@/components/PopupContent.vue'
import DocumentListener from '@/components/DocumentListener.js'
import { escapeRegExp, removeDiacritics } from '@/utils/text'

export default {
  components: { PopupContent, DocumentListener },
  props: {
    value: {},
    placeholder: String,
    disabled: Boolean,

    items: Array,
    itemText: {
      type: String,
      default: 'text'
    },
    itemValue: {
      type: String,
      default: 'value'
    },
  },
  data () {
    return {
      open: false,
      bounds: null,
      highlightIndex: -1,
      focus: false,
      search: ''
    }
  },
  computed: {
    selected () {
      const index = this.items.findIndex(item => item[this.itemValue] === this.value)
      const item = this.items[index]
      return { index, item }
    }
  },
  watch: {
    focus (v) {
      this.$emit('focus', v)
    }
  },
  methods: {
    onBlur () {
      if (!this.open) {
        this.focus = false
      }
    },
    openPopup () {
      const bounds = this.$el.getBoundingClientRect()
      this.bounds = bounds.toJSON()
      this.open = true
    },
    closePopup () {
      this.open = false
    },
    toggle () {
      this.open ? this.closePopup() : this.openPopup()
    },
    popupClick (e) {
      if (!this.$el.contains(e.target)) {
        this.closePopup()
        this.focus = false
      }
    },
    highlight (index) {
      this.highlightIndex = Math.max(0, Math.min(this.items.length - 1, index))
      const el = this.$refs.item[this.highlightIndex]
      el && el.scrollIntoView({ block: 'nearest' })
    },
    onEnter () {
      if (this.highlightIndex !== -1) {
        this.select(this.highlightIndex)
        this.closePopup()
      }
    },
    focusComponent () {
      if (!this.disabled) {
        this.$el.focus()
      }
    },
    select (index) {
      const item = this.items[index]
      item && this.selectItem(item)
    },
    selectItem (item) {
      this.$emit('input', this.itemValue ? item[this.itemValue] : item)
      this.focusComponent()
    },
    _clearSearch: debounce(function () {
      this.search = ''
    }, 400),
    keyHandler (e) {
      if (e.key.length === 1) {
        this.search += e.key
        this._clearSearch()
        const action = this.open ? this.highlight : this.select
        const currentIndex = this.open ? this.highlightIndex : this.selected.index
        const re = new RegExp('^' + escapeRegExp(removeDiacritics(this.search)), 'i')
        const testItem = item => re.test(removeDiacritics(item[this.itemText]))
        if (this.search.length === 1 && currentIndex !== -1) {
          const next = this.items[currentIndex + 1]
          if (next && testItem(this.items[currentIndex]) && testItem(next)) {
            action(currentIndex + 1)
            return
          }
        }
        const index = this.items.findIndex(testItem)
        if (index !== -1) {
         action(index)
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.select {
  display: flex;
  margin: 4px;
  padding: 0 8px;
  height: 30px;
  align-items: center;
  border: 1px solid #ddd;
  cursor: default;

  &[disabled] {
    opacity: 0.5;
    pointer-events: none;
  }
  .placeholder {
    opacity: 0.6;
  }
  svg {
    padding: 4px;
    margin-left: 10px;
    transition: 0.25s transform ease;
    path {
      fill: currentColor;
      fill-opacity: 0.7;
    }
  }
  &.open {
    svg {
      transform: rotate(180deg);
    }
  }
}
</style>
