export function pxObj (obj) {
  const val = {}
  Object.keys(obj).forEach(k => {
    val[k] = Math.round(obj[k]) + 'px'
  })
  return val
}

export function popupFixedPosition (container, activator, popupEl) {
  const pos =  {}
  const info = { dirY: 1 }
  const view = {
    left: 0,
    top: 0,
    right: window.innerWidth,
    bottom: window.innerHeight
  }
  const cBox = container.getBoundingClientRect()
  const aBox = activator.getBoundingClientRect()
  const pBox = popupEl.getBoundingClientRect()
  const popupHeight = popupEl.scrollHeight //Math.max(pBox)
  pos.left = aBox.left
  const offset = 0
  pos.top = Math.max(0, (aBox.bottom - offset))
  const bottomOverflow = Math.max(0, pos.top + popupHeight - view.bottom)
  if (bottomOverflow > 0) {
    info.maxHeight = view.bottom - aBox.bottom - 8
    // pos.top = aBox.top - pBox.height
    const topOverflow = Math.max(0, view.top - (aBox.top - popupHeight))
    if (topOverflow < bottomOverflow) {
      delete pos.top
      pos.bottom = view.bottom - aBox.top
      info.maxHeight = aBox.top - 8
      info.dirY = -1
    }
  }
  if (pos.left + pBox.width > view.right) {
    delete pos.left
    pos.right = cBox.right - aBox.right
  }
  return { position: pos, info }
}

