export function popupAbsolutePosition (container, activator, popupEl) {
  const pos =  {}
  const view = {
    left: 0,
    top: 0,
    right: window.innerWidth,
    bottom: window.innerHeight
  }
  const cBox = container.getBoundingClientRect()
  const aBox = activator.getBoundingClientRect()
  const pBox = popupEl.getBoundingClientRect()
  pos.left = aBox.left
  const offset = 0
  pos.top = Math.max(0, (container.scrollTop + aBox.bottom - offset))
  if (pos.top + pBox.height > view.bottom) {
    console.log('bottom overflow')
    pos.top = container.scrollTop + aBox.top - pBox.height
  }
  if (pos.left + pBox.width > view.right) {
    console.log('right overflow')
    delete pos.left
    pos.right = cBox.right - aBox.right
  }
  return pos
}