<template>
  <div class="popup">
    <portal to="popup">
      <template v-if="open">
        <div
          v-if="backdrop"
          class="popup-backdrop"
          v-backdrop-scroll
        >
          <div/>
        </div>
        <document-listener
          @click="onDocumentClick"
          @keydown="$emit('keydown', $event)"
        />
      </template>
      <transition
        :name="transitionName"
        v-on="transitionHooks"
        @enter="enter"
        @after-leave="leave"
      >
        <div
          v-if="open"
          ref="menuEl"
          class="popup-content"
          :class="popupClass"
          :style="contentStyle"
          @click="onClick"
        >
          <slot/>
        </div>
      </transition>
    </portal>
  </div>
</template>

<script>
// import throttle from 'lodash/throttle'
import DocumentListener from '@/components/DocumentListener.js'
import { popupFixedPosition2, pxObj } from '@/utils/popup'

function transformBounds (bounds, anchorInit, anchorCurrent) {
  const offsetY = anchorCurrent.top - anchorInit.top
  const { top, left, bottom, right } = bounds
  return {
    top: top + offsetY,
    bottom: bottom + offsetY,
    left,
    right
  }
}

export default {
  components: {
    DocumentListener
  },
  directives: {
    backdropScroll: {
      inserted (el) {
        el.scrollTop = 1
        el.addEventListener('scroll', () => {
          el.scrollTop = 1
        })
      }
    }
  },
  props: {
    open: {
      type: Boolean,
      default: undefined
    },
    bounds: Object,
    backdrop: Boolean,
    popupClass: {
      type: [String, Array, Object],
      default: 'popup-container'
    },
    transition: {
      type: [String, Object],
      default: 'fade'
    }
  },
  data () {
    return {
      contentStyle: {}
    }
  },
  computed: {
    transitionName () {
      return typeof this.transition === 'string' ? this.transition : undefined
    },
    transitionHooks () {
      return typeof this.transition === 'object' ? this.transition : {}
    }
  },
  mounted () {
    const containers = []
    let el = this.$el.parentElement
    while (el) {
      if (el.classList.contains('scrollable')) {
        containers.push(el)
      }
      el = el.parentElement
    }
    // const onScroll = throttle(() => this.updatePosition(this.$refs.menuEl), 20)
    const onScroll = () => this.updatePosition(this.$refs.menuEl)
    ;[window].concat(containers).forEach(el => {
      el.addEventListener('scroll', onScroll)
    })
    this.$once('hook:beforeDestroy', () => {
      [window].concat(containers).forEach(el => {
        el.removeEventListener('scroll', onScroll)
      })
    })
    this._containers = containers
  },
  methods: {
    updatePosition (el) {
      if (!el) {
        return
      }
      const bounds = transformBounds(this.bounds, this._openBounds, this.$el.getBoundingClientRect())
      const { position, info } = popupFixedPosition2(document.scrollingElement, bounds, el)
      position.minWidth = this.bounds.width
      // position.minWidth = this._target.offsetWidth
      // position.maxHeight = 400
      if (!this._lastInfo || this._lastInfo.dirY !== info.dirY) {
        position.maxHeight = info.maxHeight
        this.contentStyle = pxObj(position)
      } else {
        Object.assign(this.contentStyle, pxObj(position))
        // this.contentStyle = { ...this.contentStyle, ...pxObj(position)}
      }
      this._lastInfo = info
    },
    enter (el) {
      this._openBounds = this.$el.getBoundingClientRect()
      this.updatePosition(el)
      this.$emit('opened')
    },
    leave () {
      this.contentStyle = {}
      this._lastInfo = null
      this.$emit('closed')
    },
    onDocumentClick (e) {
      // e.composedPath().includes(this.$el)
      if (!this.$refs.menuEl.contains(e.target)) {
        this.$emit('click:out', e)
      } else {
        // this.$emit('click:in', e)
      }
    },
    onClick (e) {
      this.$emit('click:in', e)
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-content {
  position: absolute;
  z-index: 2;
  // transition: 0.05s;
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  background-color: #fff;
  outline: 0;
  // border-bottom-left-radius: 3px;
  // border-bottom-right-radius: 3px;
  border-radius: 3px;
  margin: 1px 0;
  max-height: 70vh;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.popup-backdrop {
  background-color: rgba(0, 0, 0, 0.12);
  position: fixed;
  top: 0;
  left: 0;
  right: -20px;
  bottom: 0;
  z-index: 1;
  overflow: auto;
  > div {
    width: 5px;
    height: 110%;
  }
}
</style>
