<template>
  <div
    class="autocomplete"
    :class="{focus, open: showPopup}"
    :disabled="disabled"
    :aria-expanded="showPopup ? 'true' : 'false'"
  >
    <input
      ref="inputEl"
      v-model="text"
      v-bind="$attrs"
      :placeholder="placeholder"
      :disabled="disabled"
      @click="openPopup"
      @focus="focus = true"
      @blur="focus = false"
      @input="openPopup"
      @keydown.down.prevent="openPopup"
    />
    <popup-content
      :transition="transition"
      :open="showPopup"
      :bounds="bounds"
      @click:out="popupClick"
      @click:in="closePopup"
      @keydown.esc="closePopup"
      @keydown.tab.prevent="closePopup"
      @keydown.up.prevent="highlight(highlightIndex - 1)"
      @keydown.down.prevent="highlight(highlightIndex + 1)"
      @keydown.enter="onEnter"
      @opened="highlight(selected.index)"
    >
      <div
        class="popup-list"
        role="listbox"
        tabindex="-1"
      >
        <template v-for="(item, index) in displayedItems">
          <div
            :key="index"
            ref="item"
            tabindex="0"
            class="item"
            :class="{
              highlight: highlightIndex === index,
              selected: selected.item === item
            }"
            @click="selectItem(item)"
          >
            <slot
              name="item"
              :item="item"
              :index="index"
              :html="highlights[index]"
              :selected="selected.item === item"
            >
              <span v-html="highlights[index]"/>
            </slot>
          </div>
        </template>
      </div>
    </popup-content>
  </div>
</template>

<script>
import PopupContent from '@/components/PopupContent.vue'

import { sanitize, escapeRegExp, removeDiacritics } from '@/utils/text'

function _highlight (orig, search, re) {
  const m = re.exec(search)
  if (m) {
    const s = m.index
    const e = s + m[0].length
    return orig.substring(0, s) + `<strong>${orig.substring(s, e)}</strong>` + _highlight(orig.substring(e), search.substring(e), re)
  }
  return orig
}

function highlight (text, query) {
  text = sanitize(text)
  if (query.length === 0) {
    return text
  }
  const searchText = sanitize(removeDiacritics(text))
  const re = new RegExp(removeDiacritics(query), 'gi')
  return _highlight(text, searchText, re)
}

export default {
  components: { PopupContent },
  inheritAttrs: false,
  props: {
    value: {},
    placeholder: String,
    disabled: Boolean,
    transition: {
      type: [String, Object],
      default: 'slide-y'
    },

    items: Array,
    itemText: {
      type: String,
      default: 'text'
    },
    itemValue: {
      type: String,
      default: 'value'
    },
    minChars: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      text: '',
      // same as Select2
      focus: false,
      open: false,
      highlightIndex: -1
    }
  },
  computed: {
    showPopup () {
      return !!(this.open && this.text.length >= this.minChars && this.items)
    },
    bounds () {
      return this.showPopup ? this.$el.getBoundingClientRect().toJSON() : null
    },
    regex () {
      // return this.text && new RegExp(escapeRegExp(this.text), 'i')
      return this.text && new RegExp(escapeRegExp(sanitize(removeDiacritics(this.text))), 'i')
    },
    displayedItems () {
      if (!this.text) {
        return this.items
      }
      // return this.items.filter(i => i[this.itemText].match(this.regex))
      return this.items.filter(i => this.regex.test(removeDiacritics(i[this.itemText])))
    },
    highlights () {
      const texts = this.displayedItems.map(item => item[this.itemText])
      if (this.text.length > 1) {
        return texts.map(text => highlight(text, this.text))
      }
      return texts
    },
    selected () {
      const index = this.items.findIndex(item => item[this.itemValue] === this.value)
      const item = this.items[index]
      return { index, item }
    }
  },
  watch: {
    focus (v) {
      this.$emit('focus', v)
    }
  },
  methods: {
    onInput (value) {
      // or watch value and set text by it?
      this.text = this.displayedItems.find(item => item[this.itemValue] === value)[this.itemText]
      this.$emit('input', value)
    },
    selectItem (item) {
      this.onInput(this.itemValue ? item[this.itemValue] : item)
      this.focusComponent()
    },
    openPopup () {
      this.open = true
    },
    // same as Select2
    closePopup () {
      this.open = false
    },
    popupClick (e) {
      if (!this.$el.contains(e.target)) {
        this.closePopup()
        this.focus = false
      }
    },
    focusComponent () {
      if (!this.disabled) {
        // this.$el.querySelector('[tabindex]').focus()
        this.$refs.inputEl.focus()
      }
    },
    // almost same as Select2 (items -> displayedItems)
    highlight (index) {
      this.highlightIndex = Math.max(0, Math.min(this.displayedItems.length - 1, index))
      const el = this.$refs.item[this.highlightIndex]
      el && el.scrollIntoView({ block: 'nearest' })
    },
    onEnter () {
      if (this.highlightIndex !== -1) {
        this.selectItem(this.displayedItems[this.highlightIndex])
        this.closePopup()
      }
    },
  }
}
</script>

<style lang="scss" scoped>
.autocomplete {
  margin: 4px;
}
input {
  height: 30px;
  // margin: 4px;
  padding: 0 8px;
  border: 1px solid #ddd;
}
</style>
